<template>
	<div ref="wrapper">
		<div>
			<slot></slot>
		</div>
	</div>
</template>

<script>
	import BScroll from "better-scroll"

    export default {
        name: "Scroll",
		props:{
			probeType:{
				type:Number,
				default:0
			},
			data: {
				type: Array,
				default: null
			},
		},
		data(){
			return{
				scroll:null
			}
		},
		mounted(){
			//获取元素
			let wrapper=this.$refs.wrapper
			//使用bscroll
			this.scroll=new BScroll(wrapper,{
				probeType:this.probeType,			//允许监听滚动的位置
				click:true,							//允许内部的标签点击
				pullUpLoad:true,					//下拉到底部
				observeDOM:true
			})
			
			//监听滚动
			this.scroll.on("scroll",(position)=>{
				//发送出去
				this.$emit("bscroll",position)
			})
			//监听下拉加载更多
			this.scroll.on("pullingUp",()=>{
				//发送出去
				this.$emit("pullingup")
			})
		},
		methods:{
			//滚动到指定位置
			scrollTo(x, y, time=300) {
               this.scroll && this.scroll.scrollTo(0, 0, time)
            },
			//下拉加载更多
			finishPullUp(){
				this.scroll && this.scroll.finishPullUp()
			},
			//刷新一下
			refresh(){
				//console.log("刷新一下")
				this.scroll && this.scroll.refresh()
			}
		},
		watch:{
			data(){
				setTimeout(() => {
					this.refresh()
				},500)
			}
		}
    }
</script>

